<template>
  <div class="main">
    <header>
      智慧商城
    </header>
    <van-search placeholder="请输入搜索关键词" @click="$router.push('/search')"/>
  <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
    <van-swipe-item v-for="item in scroll" :key="item.imgUrl"><img :src="item.imgUrl"></van-swipe-item>
  </van-swipe>
  <van-grid :column-num="5">
  <van-grid-item v-for="item in nav" :key="item.imgUrl" :icon="item.imgUrl" :text="item.text"/>
  </van-grid>
  <div class="image">
    <img :src="ad.imgUrl">
  </div>
  <div class="like">
    <h2>———猜你喜欢———</h2>
      <ShopDetail v-for="item in shopList" :key="item.goods_id" :goods="item"></ShopDetail>
  </div>
  </div>
</template>
<script>
import ShopDetail from '@/components/ShopDetail.vue'
import { getIndex } from '@/api/index'
export default {
  data () {
    return {
      scroll: [],
      nav: [],
      shopList: [],
      ad: null
    }
  },
  components: {
    ShopDetail
  },
  async created () {
    const { data: { pageData } } = await getIndex()
    this.scroll = pageData.items[1].data
    this.nav = pageData.items[3].data
    this.shopList = pageData.items[6].data
    this.ad = pageData.items[4].data[0]
  }
}
</script>
