<template>
  <div class="main">
 <van-nav-bar title="商品列表" left-arrow @click-left="$router.back()"></van-nav-bar>
<van-search
  v-model="value"
  show-action
  placeholder="请输入搜索关键词"
  @search="onSearch"
>
  <template #action>
    <div @click="onSearch"><van-icon name="apps-o" size="20"/></div>
  </template>
</van-search>
<div class="choose">
  <button @click="getGoods({goodsName: value,sortType:'all'})">综合</button>
  <button @click="getGoods({goodsName: value,sortType:'sales'})">销量</button>
  <button @click="getGoods({goodsName: value,sortType:'price'})">价格</button>
</div>
<ShopDetail v-for="item in shopList" :key="item.goods_id" :goods="item"></ShopDetail>
  </div>
</template>
<script>
import ShopDetail from '@/components/ShopDetail.vue'
import { getSearchGoods } from '@/api/search'
export default {
  data () {
    return {
      value: '',
      shopList: []
    }
  },
  components: {
    ShopDetail
  },
  async created () {
    this.value = this.$route.query.name
    this.category = this.$route.query.category
    this.getGoods({
      categoryId: this.$route.query.category,
      goodsName: this.value
    })
  },
  methods: {
    async getGoods (obj) {
      const { data: { list: { data } } } = await getSearchGoods(obj)
      this.shopList = data
    },
    onSearch () {

    }
  }
}
</script>
<style scoped lang="less">
.choose{
  display: flex;
  button{
    flex:1;
    height: 50px;
    line-height: 50px;
    border:0;
    background-color: #fff;
    font-size: 16px;
    cursor: pointer;
  }
}
</style>
