<template>
  <div class="main">
  <van-nav-bar title="全部分类" ></van-nav-bar>
  <van-search placeholder="请输入搜索关键词" @click="$router.push('/search')"/>
  <div class="detail">
    <ul class="list">
      <li v-for="(item,index) in list" :key="item.category_id" :class="{'active':index===isActive}" @click="getImg(index)">{{ item.name }}</li>
    </ul>
    <ul class="imgs" >
      <div class="img" v-for="item in imgList" :key="item.category_id" @click="$router.push(`/searchlist?category=${item.category_id}`)">
        <img :src="item.image.external_url">
      <span>{{ item.name }}</span>
      </div>
    </ul>
  </div>
  </div>
</template>
<script>
import { getList } from '@/api/category'
export default {
  data () {
    return {
      list: [],
      imgList: [],
      isActive: '',
      primeIndex: 0
    }
  },
  async created () {
    const { data: { list } } = await getList()
    this.list = list
    this.getImg(this.primeIndex)
  },
  methods: {
    getImg (index) {
      this.isActive = index
      this.imgList = this.list[index].children
    }
  }
}
</script>
<style scoped lang="less">
  .detail{
    display: flex;
    .list{
      flex:1;
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: #f3f3f3;
      height: 520px;
      overflow: scroll;
      li{
        width: 100%;
        height:40px;
        line-height: 40px;
        text-align: center;
      }
    }
    .imgs{
      flex:3;
      display: flex;
      flex-wrap: wrap;
      height: 520px;
      overflow: scroll;
      .img{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 30%;
        margin: 3px;
        img{
          width: 100%;
        }
      }
    }
  }
  .list .active{
  background-color: #fff;
  color: rgb(235, 83, 83);
  }
</style>
