<template>
  <div class="main">
    <van-nav-bar title="商品详情页" left-arrow @click-left="$router.back()"></van-nav-bar>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
    <van-swipe-item v-for="item in goods.goods_images" :key="item.file_id"><img :src="item.external_url"></van-swipe-item>
</van-swipe>
  <div class="title">
  <i>￥{{ goods.goods_price_min }}</i>
  <span>￥{{ goods.goods_price_max }}</span>
  <b>已售{{ goods.goods_sales }}件</b>
  <p class="multiline-ellipsis">{{ goods.goods_name}}</p>
  </div>
  <div class="detail">
 <div class="quit">
   <van-icon name="certificate" size="20" color="#d5b3b5"/>
  <span>七天无理由退货</span>
 </div>
 <div class="send">
    <van-icon name="certificate" size="20" color="#d5b3b5"/>
    <span>48小时发货</span>
 </div>
  <div class="go">
     <van-icon name="arrow" size="20"/>
  </div>
  </div>
  <div class="evaluation" >
    <div class="header">
      <h3>商品评价({{ limit }}条)</h3>
      <span>查看更多<van-icon name="arrow" size="20"/></span>
    </div>
    <div class="users" v-for="item in comment" :key="item.comment_id">
      <div class="icon">
        <img :src="item.user.avatar_url||defaultIcon">
      </div>
      <div class="name">
        {{ item.user.nick_name}}
         <van-rate
    :value="item.score/2"
    :size="16"
    color="#ffd21e"
    void-icon="star"
    void-color="#eee"
    />
      </div>
      <p>{{ item.content }}</p>
      <div class="time">
        {{ item. create_time}}
      </div>
    </div>
  </div>
  <div class="meau">
    <div class="home">
      <van-icon name="wap-home-o" size="25"/>
      <span>首页</span>
    </div>
    <div class="shopcar">
      <van-icon name="shopping-cart-o" size="25"/>
      <span>购物车</span>
    </div>
  <button class="join">加入购物车</button>
  <button class="buy">立即购买</button>
  </div>
  </div>
</template>
<script>
import { getDetailGoods, getGoodsComments } from '@/api/detail'
import defaultIcon from '@/assets/默认头像.png'
export default {
  data () {
    return {
      goods: [],
      comment: [],
      limit: 10,
      defaultIcon
    }
  },
  async created () {
    const { data: { detail } } = await getDetailGoods(this.$route.params.id)
    this.goods = detail
    const { data: { list } } = await getGoodsComments({ goodsId: this.$route.params.id, limit: this.limit })
    this.comment = list
  }
}
</script>
<style scoped lang="less">
.main {
  margin-bottom: 70px;
}
.my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
    img{
      width: 100%;
    }
  }
  .title{
    margin-top: 20px;
    padding: 0 10px;
    i{
      display: inline-block;
      margin-right: 10px;
      font-size: 16px;
      color:#c26766;
   }
  span{
      text-decoration: line-through;
    }
  p{
      margin-bottom: 7px;
      font-size: 16px;
      color:#000;
    }
  b{
    float: right;
  }
  }
  .detail{
    overflow: hidden;
    margin-top: 20px;
    .quit,.send{
      float: left;
      margin: 0 10px;
    }
    .go{
      float: right;
      margin-top: 5px;
      margin-right: 20px;
    }
  }
  .evaluation{
    .header{
      overflow: hidden;
      margin:20px 0;
      padding: 0 20px;
      h3{
        float: left;
      }
      span{
        float: right;
        font-size: 16px;
      }
    }
    .users{
    margin-bottom: 30px;
    overflow: hidden;
    padding: 0 20px;
    .icon,.name{
      float: left;
      margin-left: 10px;
      height: 30px;
      line-height: 30px;
    }
    .name{
      width: 85%;
    }
    .icon{
      overflow: hidden;
      width: 30px;
      height: 30px;
      border-radius: 15px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    p{
      margin: 40px 0 10px 10px;
      font-size: 18px;
    }
    .time{
      margin-left: 10px;
      color: #cbcbcb;
    }
  }
  }
.meau{
  position: fixed;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 70px;
  background-color: #fff;
  .home,.shopcar{
    flex:2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    font-size: 18px;
  }
  button{
    flex:3;
    height: 50px;
    line-height: 50px;
    padding: 0 15px;
    margin: 0 3px;
    border-radius: 25px;
    border:0;
    color: #fff;
    font-size: 16px;
  }
  .join{
    background-color: #ffa802;
  }
  .buy{
    background-color: #ff642b;
  }
}
</style>
