import MyShopDetail from '@/views/detail/MyShopDetail.vue'
import MyIndex from '@/views/index/MyIndex.vue'
import MyLogin from '@/views/login/MyLogin.vue'
import MyManager from '@/views/manager/MyManager.vue'
import MyPay from '@/views/pay/MyPay.vue'
import MySearch from '@/views/search/MySearch.vue'
import MySearchList from '@/views/search/MySearchList.vue'
import DeviceIist from '@/views/second/DeviceIist.vue'
import SecondIndex from '@/views/second/SecondIndex.vue'
import SecondMy from '@/views/second/SecondMy.vue'
import ShopCar from '@/views/second/ShopCar.vue'
import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/', redirect: '/secondindex'
      // path: '/', redirect: '/login'
    },
    {
      path: '/login', component: MyLogin
    },
    {
      path: '/index',
      component: MyIndex,
      children: [{
        path: '/secondindex', component: SecondIndex
      },
      {
        path: '/devicelist', component: DeviceIist
      },
      {
        path: '/shopcar', component: ShopCar
      },
      {
        path: '/secondmy', component: SecondMy
      }
      ]
    },
    {
      path: '/search', component: MySearch
    },
    {
      path: '/searchlist', component: MySearchList
    },
    {
      path: '/detail/:id', component: MyShopDetail
    },
    {
      path: '/pay', component: MyPay
    },
    {
      path: '/manage', component: MyManager
    }
  ],
  linkActiveClass: 'active',
  linkExactActiveClass: 'exact-active'
})
const tokenRequire = ['/pay', '/manage']
router.beforeEach((to, from, next) => {
  if (!tokenRequire.includes(to.path)) {
    next()
    return ''
  }
  if (store.getters.token) {
    next()
    return ''
  }
  next('/login')
})
export default router
