import { getItem, setItem } from '@/utils/storage'
export default {
  namespaced: true,
  state () {
    return {
      userInfo: getItem()
    }
  },
  getters: {
  },
  mutations: {
    changeToken (state, obj) {
      state.userInfo = obj
      setItem(obj)
    }
  },
  actions: {
  }
}
