import request from '@/utils/request'
export function getConfirmCode () {
  return request.get('/captcha/image', {
    headers: {
      platform: 'H5'
    }
  })
}
export function getEmailCode (code, key, number) {
  return request.post('/captcha/sendSmsCaptcha', {
    form: {
      captchaCode: code,
      captchaKey: key,
      mobile: number
    }
  },
  {
    headers: {
      platform: 'H5'
    }
  })
}
export function getLogin (number) {
  return request.post('/passport/login', {
    form: {
      isParty: false,
      mobile: number,
      smsCode: '246810',
      partyData: {}
    }
  },
  {
    headers: {
      platform: 'H5'
    }
  })
}
