<template>
  <div class="main">
  <van-nav-bar title="商品搜索" left-arrow @click-left="$router.back()"></van-nav-bar>
 <van-search
  v-model.trim="value"
  show-action
  placeholder="请输入搜索关键词"
  @search="goSearch(value)"
>

  <template #action>
    <div @click="goSearch(value)">搜索</div>
  </template>
</van-search>
<div v-if="this.history.length>0" class="history">
<div class="throw" @click="clear">
  <van-icon name="close" />
</div>
<h2>最近搜索</h2>
<ul class="historydetail">
  <li v-for="item in history" :key="item.id" @click="goSearch(item.name)">{{ item.name }}</li>
</ul>
</div>
  </div>
</template>
<script>
import { Toast } from 'vant'
import { getHistory, setHistory } from '@/utils/storage'
export default {
  data () {
    return {
      history: getHistory(),
      value: ''
    }
  },
  methods: {
    goSearch (value) {
      if (!value) {
        return Toast({
          message: '请输入正确的手机号码',
          icon: 'warn-o'
        })
      }
      const index = this.history.findIndex(item => item.name === value)
      if (index !== -1) { this.history.splice(index, 1) }
      this.history.unshift({ id: +new Date(), name: value })
      setHistory(this.history)
      this.value = ''
      this.$router.push(`/searchlist?name=${value}`)
    },
    clear () {
      this.history = []
      setHistory(this.history)
    }
  }
}
</script>
<style scoped lang="less">
  .history{
    position: relative;
    margin-top: 20px;
    .throw{
      position: absolute;
      right: 20px;
      cursor: pointer;
      transform: scale(1.5);
    }
    h2{
      margin-left: 20px;
      font-weight: 400;
    }
    .historydetail{
      margin-top: 20px;
      ul{
        display: flex;
      }
      li{
        flex:1;
        display: inline-block;
        width: 110px;
        height: 30px;
        line-height: 30px;
        margin: 10px 5px;
        background-color: #f3f5f7;
        border-radius: 15px;
        text-align: center;
        font-size: 16px;
      }
    }
  }
</style>
