import request from '@/utils/request'
export const getDetailGoods = goodsId => {
  return request.get('/goods/detail', {
    params: {
      goodsId
    }
  })
}

export const getGoodsComments = obj => {
  const { goodsId, limit } = obj
  return request.get('/comment/listRows', {
    params: {
      goodsId,
      limit
    }
  })
}
