<template>
  <div class="main">
    <van-nav-bar title="会员登陆" left-arrow @click-left="$router.back()"></van-nav-bar>
    <div class="content">
      <h1>手机号登陆</h1>
      <div class="tip">未注册的手机号登陆后将自动注册</div>
      <div class="info">
        <input type="text" placeholder="请输入手机号码" v-model="phoneNumber">
        <input type="text" placeholder="请输入图形验证码" v-model="userKey">
        <img :src="imgUrl" v-if="imgUrl" @click="getRequest()">
        <input type="text" placeholder="请输入短信验证码" v-model="email">
        <button class="get" @click="getEmail">
          {{ second===totalSecond?'获取验证码':`重新发送(${second})秒` }}
        </button>
      </div>
      <button class="login" @click="getMyLogin">登录</button>
    </div>
  </div>
</template>
<script>
import { getConfirmCode, getEmailCode, getLogin } from '@/api/login'
import { Toast } from 'vant'
import { mapMutations } from 'vuex'
export default {
  data () {
    return {
      imgUrl: '',
      key: '',
      userKey: '',
      phoneNumber: '',
      totalSecond: 60,
      second: 60,
      timeID: null,
      email: ''
    }
  },
  methods: {
    ...mapMutations('user', ['changeToken']),
    async getRequest () {
      const { data: { base64, key } } = await getConfirmCode()
      this.imgUrl = base64
      this.key = key
    },
    getExam () {
      if (!/^1(3\d|4[5-9]|5[0-35-9]|6[567]|7[0-8]|8\d|9[0-35-9])\d{8}$/.test(this.phoneNumber)) {
        Toast({
          message: '请输入正确的手机号码',
          icon: 'warn-o'
        })
        return false
      }
      if (!/^\w{4}$/.test(this.userKey)) {
        Toast({
          message: '请输入正确的验证码',
          icon: 'warn-o'
        })
        return false
      }
      return true
    },
    async getEmail () {
      if (!this.getExam()) return
      if (!this.timeID && this.second === this.totalSecond) {
        await getEmailCode(this.userKey, this.key, this.phoneNumber)
        Toast({
          message: '发送成功，请注意查收',
          icon: 'certificate'
        })
        this.timeID = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timeID)
            this.timeID = null
            this.second = this.totalSecond
          }
        }, 1000)
      }
    },
    async getMyLogin () {
      if (!this.getExam() || !/^\d{6}$/.test(this.email)) {
        Toast({
          message: '请输入正确的短信验证码',
          icon: 'warn-o'
        })
        return
      }
      const res = await getLogin(this.phoneNumber)
      this.changeToken(res.data)
      Toast({
        message: '登陆成功',
        icon: 'certificate'
      })
      this.$router.push('/index')
    }
  },
  created () {
    this.getRequest()
  },
  destroyed () {
    clearInterval(this.timeID)
  }
}
</script>
<style scoped lang="less">
.content{
  margin-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  .tip{
    color: #99979b;
  }
  .info{
    margin-top: 50px;
    margin-bottom: 100px;
    img{
      width: 100px;
      height: 50px;
    }
    .get{
      height: 30px;
      line-height: 30px;
      padding:0 20px;
      border:0;
      background-color: #fff;
      color: #d79748;
    }
  }
  .login{
    width: 100%;
    height: 50px;
    line-height: 50px;
    border-radius: 25px;
    border:0;
    background-color: #f7ae2d;
    color: #fff;
    font-size: 18px;
  }
}
.info input:first-child{
  display: block;
  width: 100%;
  height: 50px;
  line-height: 50px;
  border:0;
  border-bottom: 1px solid #fbfbfb;
}
.info input:nth-of-type(n+2){
  width: 200px;
  height: 50px;
  line-height: 50px;
  border:0;
  border-bottom: 1px solid #fbfbfb;
}
</style>
